/* src/App.css */

.App {
  text-align: center;
  font-family: 'Arial', sans-serif;
  color: #333;
}

.App-header {
  background-color: #282c34;
  padding: 50px;
  color: white;
}

.App-header h1 {
  font-size: 2.5rem;
  margin: 0;
}

.App-header p {
  font-size: 1.2rem;
  margin: 20px 0;
}

.App-link {
  color: #61dafb;
  font-size: 1rem;
  text-decoration: none;
  border: 2px solid #61dafb;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

.App-link:hover {
  background-color: #61dafb;
  color: white;
}

.App-features {
  display: flex;
  justify-content: center;
  padding: 50px 0;
  background-color: #f7f7f7;
}

.feature-card {
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  margin: 0 15px;
  width: 200px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.feature-card h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.feature-card p {
  font-size: 1rem;
  color: #666;
}
